<template>
  <div class="order-wrap">
    <h3>您的产品</h3>
    <div class="order-list-choose">
      <div class="order-list-option">
        选择产品：
        <v-selection :selections="products" @on-change="productChange"></v-selection>
      </div>

      <div class="order-list-option">
        开始日期：
        <v-date-picker v-model="startTime" @input="getStartDate"></v-date-picker>
      </div>

      <div class="order-list-option">
        结束日期：
        <v-date-picker v-model="endTime" @input="getEndDate"></v-date-picker>
      </div>

      <div class="order-list-option">
        关键词：<!--.lazy修饰符指定鼠标离开时触发watch去修改内容-->
        <input type="text" v-model.lazy="query" class="order-query">
      </div>
    </div>
    <div class="order-list-table">
      <table>
        <tr>
          <th v-for="head in tableHeads" @click="changeOrderType(head)" :class="{active:head.active}">
            {{ head.label }}
          </th>
        </tr>
        <tr v-for="item in tableData" :key="item.period">
          <td v-for="head in tableHeads">{{ item[head.key] }}</td>
        </tr>
      </table>
    </div>
    <div class="pages">
      <v-chooser :selections="pages"></v-chooser>
    </div>
  </div>
</template>

<script>
  import VDatePicker from 'vue-datepicker-local'
  import VSelection from '../components/base/selection'
  import VChooser from '../components/base/chooser'
  import {eventBus} from "../eventBut";
  import _ from 'lodash'

  export default {
    name: "orderList",
    components: {
      VDatePicker,
      VSelection,
      VChooser
    },
    data() {
      return {
        endTime: new Date(),
        startTime: new Date(),
        query: '',
        productId: 0,
        currentOrder: 'desc',
        products: [
          {
            label: '数据统计',
            value: 0
          },
          {
            label: '数据预测',
            value: 1
          },
          {
            label: '流量分析',
            value: 2
          },
          {
            label: '广告发布',
            value: 3
          }
        ],
        tableHeads: [{
          label: '订单号',
          key: 'orderId'
        },
          {
            label: '购买产品',
            key: 'product'
          },
          {
            label: '版本类型',
            key: 'version'
          },
          {
            label: '有效时间',
            key: 'period'
          },
          {
            label: '购买日期',
            key: 'date'
          },
          {
            label: '数量',
            key: 'buyNum'
          },
          {
            label: '总价',
            key: 'amount'
          }],
        pages: [{
          label: 1,
          value: 0
        },
          {
            label: 2,
            value: 1
          },
          {
            label: 3,
            value: 2
          },
          {
            label: 4,
            value: 3
          },
          {
            label: 5,
            value: 4
          },
          {
            label: 6,
            value: 5
          },
          {
            label: 7,
            value: 6
          },
          {
            label: '首页',
            value: 7
          },
          {
            label: '末页',
            value: 8
          },
        ]
      }
    },
    watch: {
      query() {
        this.$store.dispatch('fetchOrderList')
      }
    },
    computed: {
      tableData: {
        get() {
          return this.$store.getters.getOrderList
        },
        set() {

        }
      }
    },
    mounted() {
      eventBus.$on('res-component', () => {
        this.tableHeads.map((item) => {
          item.active = false;
          return item
        });
      })
      this.$store.dispatch('fetchOrderList');
    },
    methods: {
      productChange(obj) {
        // this.productId = obj.value;
        this.$store.commit('updateParams', {
          key: 'productId',
          val: obj.value
        })
        this.$store.dispatch('fetchOrderList')
      },
      getStartDate(date) {
        this.startTime = date;
        this.$store.commit('updateParams', {
          key: 'startTime',
          val: date
        })
        this.$store.dispatch('fetchOrderList')
      },
      getEndDate(date) {
        this.endTime = date;
        this.$store.commit('updateParams', {
          key: 'endTime',
          val: date
        })
        this.$store.dispatch('fetchOrderList')
      },
      // getTableDate() {
      //   let reqParam = {
      //     query: this.query,
      //     productId: this.productId,
      //     startTime: this.startTime,
      //     endTime: this.endTime
      //   };
      //   this.$http.post('/api/getOrderList', reqParam)
      //     .then((res) => {
      //       this.tableData = res.data.list;
      //     }, (err) => {
      //     })
      // },
      changeOrderType(headItem) {
        const e = window.event || arguments.callee.caller.arguments[0];//获取event事件
        e.stopPropagation();

        this.$store.dispatch('fetchOrderList')
        this.tableHeads.map((item) => {
          item.active = false;
          return item
        });
        headItem.active = true;

        if (this.currentOrder === 'asc') {
          this.currentOrder = 'desc'
        }
        else if (this.currentOrder === 'desc') {
          this.currentOrder = 'asc'
        }
        this.tableData = _.orderBy(this.tableData, headItem.key, this.currentOrder)

      },
    }
  }
</script>

<style scoped>
  .order-wrap {
    width: 1200px;
    min-height: 800px;
    margin: 20px auto;
    overflow: hidden;
  }

  .order-wrap h3 {
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 20px;
  }

  .order-query {
    height: 25px;
    line-height: 25px;
    border: 1px solid #e3e3e3;
    outline: none;
    text-indent: 10px;
  }

  .order-list-option {
    display: inline-block;
    padding-left: 15px;
  }

  .order-list-option:first-child {
    padding-left: 0;
  }

  .order-list-table {
    margin-top: 20px;
  }

  .order-list-table table {
    width: 100%;
    background: #fff;
  }

  .order-list-table td,
  .order-list-table th {
    border: 1px solid #e3e3e3;
    text-align: center;
    padding: 10px 0;
  }

  .order-list-table th {
    background: #4fc08d;
    color: #fff;
    border: 1px solid #4fc08d;
    cursor: pointer;
  }

  .order-list-table th.active {
    background: #35495e;
  }

  .pages {
    float: right;
    margin-top: 8px;
  }
</style>



